import React, { useState, useEffect } from "react";
import people from "../StaticData/people";

export default function ChatBox(props) {
  const [person, setPerson] = useState();
  useEffect(() => {
    setPerson(people.filter((person) => person.id === props.userId)[0]);
  }, [props.userId]);
  return (
    <React.Fragment>
      {person && (
        <div className="chat-box">
          <img className="friend-image" alt="friend-img" src={person.image} />
          <a href={`/chats/${person.id}`} className="friend-info-a">
            <div className="friend-info">
              <h5 className="friend-name">Name : {person.name}</h5>
              <p className="friend-msg">Message: </p>
            </div>
          </a>
        </div>
      )}
    </React.Fragment>
  );
}
