import React, { useState, useEffect } from "react";
import ChatBox from "./ChatBox";
import { default as chatsData } from "../StaticData/chats";

import "./Chats.css";

export default function Chats(props) {
  const [chats, setChats] = useState([]);
  useEffect(() => {
    setChats(chatsData.filter((chat) => chat.id === "Batman")[0].messages);
  }, []);

  return (
    <div className="all-chat">
      {chats.length !== 0 && chats.map((userId,index) => <ChatBox key={index} userId={userId.user} />)}
    </div>
  );
}
