import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import messages from "../StaticData/messages";
import people from "../StaticData/people";
import "./Messenger.css";

export default function Messenger(props) {
  const pid = useParams().userId;
  const [input, setInput] = useState("");
  const [chats, setChats] = useState([]);
  const [person, setPerson] = useState();

  useEffect(() => {
    if (pid !== undefined) {
      setPerson(people.filter((person) => person.id === pid)[0]);
      setChats((prevValue) =>
        prevValue.concat(
          messages.filter(
            (message) =>
              message.id === `${pid}:Batman` || message.id === `Batman:${pid}`
          )[0].messages
        )
      );
    }
  }, [pid]);

  function handleTextFieldInput(event) {
    setInput(event.target.value);
  }
  function handleTextFieldSubmit(event) {
    setChats((prevValue) =>
      prevValue.concat({
        time: "2020-12-22",
        sender: "Batman",
        message: input,
      })
    );
    setInput("");
    event.preventDefault();
  }
  return (
    <div className="messenger-box">
      {person && (
        <header className="messenger-header">
          <a href="/">
            <button type="backButton" onClick={props.onClick}>
              <p>‚Üê</p>
            </button>
          </a>
          <img className="friend-image" alt="friend-img" src={person.image} />
          <div>
            <h1>{person.name} </h1>
          </div>
        </header>
      )}
      <div className="messages">
        <div className="all-chats">
          {chats.length !== 0 &&
            chats.map((message, index) => {
              return (
                <div key={index}>
                  <p className={message.sender === "Batman" ? "right" : "left"}>
                    {message.message}
                  </p>
                </div>
              );
            })}
        </div>
        <div className="textfield">
          <span>
            <p>üòä</p>
          </span>
          <form className="textfield-form" onSubmit={handleTextFieldSubmit}>
            <input type="text" onChange={handleTextFieldInput} value={input} />
            <button type="submit" />
          </form>
          <span>
            <p>üìé</p>
          </span>
          <span>
            <p>üì∑</p>
          </span>
        </div>
      </div>
    </div>
  );
}
